.search-bar {
  border: 2px solid lightgray;
  border-radius: 10px;
  padding: 15px 20px;
  width: 75%;
  font-size: 20px;
}

.search-bar__form {
  display: flex;
  justify-content: center;
  padding: 30px 0 40px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #8686864c;
}